.item {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 3vh 3vw;
}
.item__close {
  position: fixed;
  top: 6vh;
  right: 6vw;
  width: 30px;
  height: 30px;
  background: transparent;
  border: none;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  transition: 0.3s;
}
.item__close:hover {
  background: #db3a34;
  color: white;
  border: 1px solid #db3a34;
}
.item__image {
  border-radius: 15px;
  width: 40vw;
}
.item__title {
  color: #db3a34;
  font-size: 2em;
}
.item__price {
  color: #323031;
  text-transform: uppercase;
  color: white;
}
.item__description {
  width: 70vw;
  font-size: 1.1em;
  color: white;
}
.item__address {
  display: flex;
  align-items: center;
  padding: 1vh 0;
}
.item__address--icon, .item__address--url {
  color: white;
}
.item__buttons {
  display: flex;
  flex-direction: column;
}
.item__buttons a {
  background: #f6a80a;
  width: 150px;
  height: 50px;
  color: white;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}
.item__buttons a:hover {
  background: #084c61;
}

